                          color="error"
                          class="translate-x-10 -translate-y-7"
                          small
                        >
                        <v-icon
                          class="mx-1"
                          small
                        >
                        fa-solid fa-rectangle-xmark
                        </v-icon>
                          Close
                        </v-btn>
                    </div>
                  </template>
                  <v-card-text>
                    <v-row>
                      <v-col
                        cols="12"
                        md="12"
                      >
                        <v-text-field
                          v-model="uid"
                          label="UID*"
                          append-icon="fa-solid fa-id-card-clip"
                          filled
                        />
                        <v-text-field
                          v-model="fname"
                          label="Fullname*"
                          append-icon="mdi-account"
                          filled
                        />
                          <v-text-field
                          v-model="kelas"
                          label="Kelas*"
                          append-icon="fa-solid fa-school"
                          filled
                        />
                          <v-text-field
                          v-model="email"
                          type="email"
                          label="Email*"
                          append-icon="fa-solid fa-at"
                          filled
                        />
                      </v-col>
                      <v-col
                      cols="12"
                      md="12"
                      >
                        <v-btn
                          elevation-2
                          class="uppercase w-full np"
                          color="indigo lighten-2"
                          text
                        >
                          save
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </material-card>
