                                    <button class="v absolute text-xs text-green-500 font-light hidden translate-y-10"><i class="fa-solid fa-file-shield"></i> <span></span></button>
                                </div>
                                <div class="mt-5 flex justify-between mx-9">
                                  <button @click="paste" class="py-2 px-4 bg-rose-600 hover:bg-rose-800 save focus:ring-blue-500 focus:ring-offset-blue-200 w-full transition ease-in duration-200 text-white text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">Upload</button>
                                </div>
                              </div>
                            </template>
                          </v-card-text>
                        </v-tab-item>
                        <v-tab-item
                          :key="2"
                        >
