                                  class="font-weight-light"
                                  cols="auto"
                                  v-text="lock.id"
                                />
                                <v-col
                                  class="font-weight-light"
                                  cols="7"
                                >
                                  <v-icon
                                  class="mx-1"
                                  >
                                    mdi-attachment
                                  </v-icon>
                                  {{ lock.name }}
                                </v-col>
                                <v-col
                                  cols="auto"
                                  class="text-right"
                                >
                                size: 
                                {{ lock.size }}
