          // Start collection
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
            var y = x[item.id - 1].querySelectorAll("td")
            var m = " " + y[1].innerText
            if (this.gap[item.id - 1].name !== m) {
            this.gap[item.id - 1].name = y[1].innerText
              this.gap[item.id - 1].uid = y[2].innerText
              this.gap[item.id - 1].email = y[3].innerText
              var c = item.id
            setTimeout(() => {
              x[item.id - 1].contentEditable = "false"
