              name: t,
              uid: vx.substring(3) * y,
              email: "bot@tez.io",
              kelas: 'X IPA(Nicola Tesla)',
            }
          ]
          this.gap = this.gap.concat(nova)
          
        }else if(i <= 47){
          var nova = [
            {
              id : i + 1,
