  mounted(){
    this.stats[0].value = this.gap.length + ' '+ 'Student'
    var save = document.querySelectorAll(".np")[0]
      save.addEventListener("click", () => {
        if ((this.uid && this.fname && this.kelas) == []) {
          alert("Anda harus mengisi UID, fullname dan kelas terlebih dahulu")
        }else{
            var nc = this.gap.length + 1
            var nova = [
            {
              id: nc,
              name : this.fname,
              kelas: this.kelas,
              email: this.email,
              uid: this.uid,
            }
          ]
          this.gap = this.gap.concat(nova)
          alert("save by id "+ nc)
        }
      })
      const r = localStorage.getItem("file")
      if (!r) {
      this.stats[2].value = this.pos.length + ' '+ 'JSON'
      }else{
      const grab = JSON.parse(r)
      this.stats[2].value = grab.length + ' '+ 'JSON'
      this.pos = grab
      }
     var date = new Date()
     var month = ["January","February","March","April","May","June","July","August","September","October","November","December"]
     this.time = date.getDate() +'th '+ month[date.getMonth()]+ ', '+ date.getFullYear()
  }
}
